.navbar {
    z-index: 6;
    display: flex;
    top: 0;
    position: fixed;
    background-color: rgb(26, 26, 26);
    height: 10vh;
    width: 100%;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
}
.navbar a {
    color: white;
    text-decoration: none;
    height: 100%;
    display: flex;
    width: 100%;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.navbar-div {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.navbar-div:hover {
    border-bottom: 4px solid #b1f5ff;
}

.navbar-div h1 {
    pointer-events: none;
    display: flex;
    text-align: center;
    justify-content: center;
}

.menuButton {
    width: 100px;
    text-align: center;
    position: fixed;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgb(26, 26, 26);
    border-bottom: 4px solid #b1f5ff;
    user-select: none;
    z-index: 1;
}

.menuButton h1 {
    font-weight: bolder;
    color: white;
}

.menuButton:hover {
    background-color: #b1f5ff;
}

.menuButton:hover h1 {
    color: rgb(26, 26, 26);
}

