              .projetVideo {
                height: 100%;
                width: 100%;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
              }
            
            .infos {
                height: 100%;
                transition: .3s;
                transition-delay: .3s;
                text-align: center;
              } 
              
              .infos h2 {
                margin: 0;
                color: white;
                text-shadow: 0px 0px 4px rgb(214, 222, 255);
                font-family: "Roboto Mono", monospace;
                font-weight: bold;
                position: relative;
                top: 50%;
                transform: translateY(-50%);
                opacity: 0;
                transition: .3s;
                transition-delay: .3s;
                user-select: none;
                
              }
              
              
              .Border:before,
              .Border:after {
                  position: absolute;
                  width: 10px; 
                  height: 10px;
                  border-style:solid;
                  border-color: rgb(214, 222, 255); 
                  content: ' ';
                  transition: .3s;
              }
               
              .BorderTop:before {
                  top: 25px;
                  left: 25px;
                  border-width: 1px 0 0 1px
              }
              
              .BorderTop:after {
                  top: 25px;
                  right: 25px;
                  border-width: 1px 1px 0 0
              } 
              
              .BorderBottom:before {
                bottom: 25px;
                left: 25px;
                border-width: 0 0 1px 1px;
              }
              
              .BorderBottom:after {
                bottom: 25px;
                right: 25px;
                border-width: 0 1px 1px 0;
              } 
            
            
              .projectsItems:hover .BorderTop:before {
                top: 0;
                left: 0;
              }
              
              .projectsItems:hover .BorderTop:after {
                top: 0;
                right: 0;
              }
              
              .projectsItems:hover .BorderBottom:before {
                bottom: 0;
                left: 0;
              }
              
              .projectsItems:hover .BorderBottom:after {
                bottom: 0;
                right: 0;
              }
              
              .projectsItems:hover .infos {
                background-color: #04014e8b;
              }
              
              .projectsItems:hover .infos h2 {
                opacity: 1;
              }
        }
        
