.skillsWindow {
    width: calc(100% - 50px);
    height: calc(100vh - 200px);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    text-align: center;
    border-right: 5px solid #04014e;
    border-left: 5px solid #04014e;
    border-bottom: 5px solid #b1f5ff;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.skillsTitle {
    height: 20%;
    background-color: #04014e;
    color: white;
    width: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.skillsListWindow {
    width: 100%;
    height: 80%;
    overflow-y: scroll;
}

.skillsListWindow::-webkit-scrollbar-track
{
    border-left: 1px solid #04014e;
}

.skillsListWindow::-webkit-scrollbar
{
    width: 10px;
}

.skillsListWindow::-webkit-scrollbar-thumb
{
    background-color: #04014e;  
}

.skillsListWindow ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    align-content: center;
}

.skillsListWindow ul li:nth-child(1) {
    border-top: none;
}

.skillsListWindow ul li {
    border-top: 1px solid white;
    height: 100%;
    width: 50%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}

.skillsListWindow ul li h1 {
    color: white;
    text-shadow: 0px 0px 4px rgb(214 222 255);
    font-weight: bold;
}