        /* Desktops and laptops ----------- */
        @media only screen and (min-width : 1224px) {
            .contactWindow {
                background-color: #04014e7f;
                height: 80%;
                width: 50%;
                position: relative;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
            
            .contactWindow form {
                text-align: center;
                position: relative;
                top: 50%;
                transform: translateY(-50%);
            }
            
            .contactWindow form input,.contactWindow form textarea {
                text-align: center;
                background-color: #04014e;
                color: white;
                width: 100%;
                padding: 20px 20px;
                box-sizing: border-box;
                border: none;
                border-bottom: 4px solid #b1f5ff;
                font-size: 20px;
              }
            
            .contactWindow form input::placeholder,.contactWindow form textarea::placeholder {
                color: white;
              }
            
        }
        
