.about-description {
    background-color: #04014e;
    color: white;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    height: 50vh;
}

.about-cv {
    height: 50vh;
    display: flex;
    align-content: center;
    justify-content: space-around;
    align-items: center;
}


.cv {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
}

.cv1 {
    background-image: url('./CV_BASIQUE_BACKGROUND.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.cv2 {
    background-image: url('./CV_CREATIF_BACKGROUND.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.cvhover {
    background-color: rgba(0, 3, 163, .2);
    position: absolute;
    width: 100%;
    top: 0;
    height: 100%;
}

.cv:hover .cvhover {
    background-color: rgba(0, 2, 92, .5);
}

.cv h1 {
    z-index: 2;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-shadow: 0px 0px 4px rgb(214 222 255);
    font-weight: bold;
    font-size: 60px;
    user-select: none;
}