@media only screen and (max-width : 321px) {

}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width : 321px) {
    .about-description h2 {
        font-size: 20px;
        text-align: center;
    }

    .cv h1 {
        font-size: 30px;
    }
}
    
    /* Smartphones (portrait) ----------- */
    @media only screen and (max-width : 320px) {
        .about-description h2 {
            font-size: 20px;
            text-align: center;
        }

        .cv h1 {
            font-size: 30px;
        }
    }

    @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) and (-webkit-min-device-pixel-ratio : 2) {
        .about-description h2 {
            font-size: 20px;
            text-align: center;
        }
        }
        
        @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) and (-webkit-min-device-pixel-ratio : 2) {
            .about-description h2 {
                font-size: 20px;
                text-align: center;
            }
        }

        /* Desktops and laptops ----------- */
        @media only screen and (min-width : 1224px) {
            .about-description {
                background-color: #04014e;
                color: white;
                display: flex;
                align-content: center;
                justify-content: center;
                align-items: center;
                height: 50vh;
            }

            .about-description h2 {
                font-size: 20px;
                text-align: center;
            }
            
            .about-cv {
                height: 50vh;
                display: flex;
                align-content: center;
                justify-content: space-around;
                align-items: center;
            }
            
            
            .cv {
                position: relative;
                width: 100%;
                height: 100%;
                text-align: center;
            }
            
            .cv1 {
                background-image: url('./CV_BASIQUE_BACKGROUND.png');
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
            
            .cv2 {
                background-image: url('./CV_CREATIF_BACKGROUND.png');
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
            
            .cvhover {
                background-color: rgba(0, 3, 163, .2);
                position: absolute;
                width: 100%;
                top: 0;
                height: 100%;
            }
            
            .cv:hover .cvhover {
                background-color: rgba(0, 2, 92, .5);
            }
            
            .cv h1 {
                z-index: 2;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: white;
                text-shadow: 0px 0px 4px rgb(214 222 255);
                font-weight: bold;
                font-size: 60px;
                user-select: none;
            }
        }
        
        /* Large screens ----------- */
        @media only screen and (min-width : 1824px) {
            .about-description h2 {
                font-size: 40px;
                text-align: center;
            }
        }