.projectsWindow {
    height: 90%;
    width: 80%;
    background-color: #04014e7e;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow-y: scroll;
}

.projectsWindow::-webkit-scrollbar-track
{
    border-left: 1px solid #04014e;
}

.projectsWindow::-webkit-scrollbar
{
    width: 10px;
}

.projectsWindow::-webkit-scrollbar-thumb
{
    background-color: #04014e;  
}

