.bannerimg {
    height: 100vh;
    user-select: none;
}

.bannerimg h1 {
    color: white;
    z-index: 4;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 200px;
}

#myVideo {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
  }