@keyframes background {
    0% {
        background-color: rgba(1, 6, 78, 0.1);
    }
    50% {
        background-color: rgb(11, 0, 165);
    }
}

@keyframes type {
    0% {
      width: 0;
    }
    99.9% {
      border-right: .15em solid orange;
    }
    100% {
      border: none;
    }
  }

  @keyframes type2 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    99.9% {
      border-right: .15em solid orange;
    }
    100% {
      opacity: 1;
      border: none;
    }
  }

  @keyframes type3 {
    0% {
      width: 0;
    }
    1% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes type4 {
    0% {
      width: 0;
    }
    1% {
        width: .15em;
      opacity: 1;
    }
    98% {
        width: .15em;
    }
    99.9% {
        border-right: .15em solid orange;
      }
    100% {
        border: none;
      opacity: 1;
    }
  }

  @-webkit-keyframes blink {
    50% {
      border-color: tranparent;
    }
  }

  @keyframes blink {
    50% {
      border-color: transparent;
    }
  }