@media only screen and (max-width : 321px) {

    .load-scrypt p {
        font-size: 4px;
    }

    .loginAccess {
        height: 300px;
        width: 200px;

    }

    .loginAccess h1 {
        font-size: 40px;
    }

}



/* Smartphones (landscape) ----------- */
@media only screen and (min-width : 321px) {
    .load-scrypt p {
        font-size: 5px;
    }

    .loginAccess {
        height: 300px;
        width: 250px;

    }

    .loginAccess h1 {
        font-size: 50px;
    }
}
    
    /* Smartphones (portrait) ----------- */
    @media only screen and (max-width : 320px) {
    /* Styles */
    }

    @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) and (-webkit-min-device-pixel-ratio : 2) {
        .load-scrypt p {
            font-size: 10px;
        }
        }
        
        @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) and (-webkit-min-device-pixel-ratio : 2) {
            .load-scrypt p {
                font-size: 10px;
            }
        }
        /* Desktops and laptops ----------- */
        @media only screen and (min-width : 1224px) {
            .load-screen {
                cursor: progress;
                height: 100vh;
                background-color: rgb(26, 26, 26); 
                overflow-y: scroll;
                overflow-x: hidden;
            }
            
            .load-scrypt {
                    color: aliceblue;
                    user-select: none;
                    pointer-events: none;
                    padding: 10px;
            }
            
            .load-scrypt p {
                    border-right: .15em solid orange;
                    font-family: "Roboto Mono", monospace;
                    font-size: 16px;
                    white-space: nowrap;
                    overflow: hidden;
                    margin: 0;
            }
            
            .loadbar {
                letter-spacing: 1px;
                color: rgb(255, 220, 21);
                text-shadow: 0px 0px 10px rgb(212, 234, 255);
            }
            
            .loadtext {
                letter-spacing: 1px;
                color: white;
                text-shadow: 0px 0px 10px rgb(229, 242, 255);
            }
            
            .loadwait {
                color: rgb(153, 153, 153);
                text-shadow: 0px 0px 10px rgb(229, 242, 255);
            }
            
            .loaddesk {
                letter-spacing: 1px;
                color: rgb(255, 220, 21);
                text-shadow: 0px 0px 10px rgb(212, 234, 255);
            }
            
            .loginAccess {
                height: 300px;
                width: 500px;
                background-color: rgba(1, 6, 78, 0.1);
                border: 4px solid rgb(255, 255, 255);
                box-shadow: 0px 0px 5px rgb(214, 222, 255);
                border-radius: 1%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
                animation: background 1.5s infinite;
            }
            
            .loginAccess a {
                text-decoration: none;
            }
            
            .loginAccess h1 {
                color: white;
                text-shadow: 0px 0px 5px rgb(214, 222, 255);
                font-weight: bolder;
                font-family: "Roboto Mono", monospace;
                font-size: 100px;
                user-select: none;
            }
        }
        
        /* Large screens ----------- */
        @media only screen and (min-width : 1824px) {
        /* Styles */
        }